"use client";

export default function LoadingScreen() {
    return (
        <div className="fixed inset-0 bg-white z-[9999] flex items-center justify-center">
            <div className="relative">
                {/* SVG Logo Animation */}
                <svg
                    className="w-24 h-24"
                    viewBox="0 0 100 100"
                >
                    <circle
                        cx="50"
                        cy="50"
                        r="35"
                        className="fill-[#43c3ff]/20"
                    />
                    <circle
                        cx="50"
                        cy="50"
                        r="35"
                        className="stroke-[#43c3ff] stroke-2 fill-none animate-pulse"
                    />
                </svg>
                {/* Spinning Border */}
                <div className="absolute -inset-2 border-4 border-t-[#43c3ff] rounded-full animate-spin opacity-30"></div>
            </div>
        </div>
    );
}
